@font-face {
  font-family: "Oswald";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
body {
  margin: 0;
  padding: 0;
}
img {
  width: 100%;
  height: auto;
}
a {
  text-decoration: none;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page {
  height: 100%;
}
.page__body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  min-width: 320px;
  min-height: 100%;
  font-family: "Arial", "Helvetica", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #000000;
}
.container {
  padding: 0 20px;
}
@media screen and (min-width: 768px) {
  .container {
    padding: 0 30px;
  }
}
@media screen and (min-width: 1440px) {
  .container {
    max-width: 1220px;
    margin: 0 auto;
    padding: 0 110px;
  }
}
@media screen and (min-width: 768px) {
  .wrapper {
    padding: 0 70px;
  }
}
@media (min-width: 1440px) {
  .wrapper--text-group {
    padding-right: 0;
    padding-left: 110px;
  }
}
@media (min-width: 1440px) {
  .wrapper--checkbox-group {
    padding-left: 0;
    padding-right: 110px;
  }
}
.btn {
  display: block;
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #ffffff;
  background-color: #68b738;
  text-align: center;
  text-transform: uppercase;
  border: none;
  cursor: pointer;
}
.btn:hover,
.btn:focus {
  background-color: #5eaa2f;
}
.btn:active {
  background-color: #5eaa2f;
  color: rgba(255, 255, 255, 0.3);
}
.page-header {
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
}
@media (min-width: 1440px) {
  .page-header--index {
    position: relative;
    min-height: 696px;
  }
  .page-header--index::before {
    content: "";
    position: absolute;
    right: 0;
    width: 50%;
    min-height: 696px;
    background-color: rgba(104, 183, 56, 0.85);
    z-index: -1;
  }
  .page-header--index::after {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    width: 50%;
    height: 696px;
    background-image: url("../img/background-cat-desktop.jpg");
    background-repeat: no-repeat;
    background-position: left top;
    background-size: contain;
    background-color: #444444;
    z-index: -2;
  }
}
@media (min-width: 1440px) and (min-resolution: 2dppx) {
  .page-header--index::after {
    background-image: url("../img/background-cat-desktop@2x.jpg");
    background-size: contain;
  }
}
@media (max-width: 767px) {
  .page-header--border {
    border-bottom: 1px solid #d9d9d9;
  }
}
